.Main {
    /* display: table-cell; */
    text-align: center;
    padding: 15px;
    position: absolute;
    top: 50%;
    left: 50%;
    -ms-transform: translateX(-50%) translateY(-50%);
    -webkit-transform: translate(-50%,-50%);
    transform: translate(-50%,-50%);
}

.Button {
    height: 50px;
    margin: 10px;
    border-radius: 10px;
}

.User{
    height: 30px;
    width: 200px;
    padding: 5px;
    border-radius: 10px;
    border: 2px solid black;
    background: rgb(245, 245, 245);
    color: black;
    outline: none;
}