#Container{
    border: 2px solid black;
    margin: 10px;
    padding: 5px;
    position: relative;
}

#User{
    border: 2px solid black;
    width: fit-content;
    padding: 10px;
    border-radius: 10px;
}

#msg{
    border: 2px solid black;
    margin-top: 10px;
    padding: 7px;
}

.Sender {
    background: rgb(143, 143, 143);
}

.Receiver {

}

.Admin {
    background: red;
}

#Container>#delete{
    position: absolute;
    top: 0px;
    right: 0px;
    margin: 10px;
    width: fit-content;

}