textarea {
    margin: 10px;
    border: 2px solid black;
    min-height: 20%;
    height: 15vh;
    width: 97vw;
    font-size: 23px;
    outline: none;
}

@media only screen and (max-width: 500px) {
    textarea  {
        width: 93vw;
    }
}

.container{
    display: flex;
}

button{
    background: white;
    color: black;
    width: 100px;
    border: 2px solid black;
    outline: none;
}

button:active{
    background: rgb(196, 196, 196);
}