.Chat {
    
    margin-top: 5.8vh;
    /* margin: 10px; */
    background: rgb(240, 240, 240);
    /* border: 2px solid black; */
    height: 76vh;
    max-height: 80vh;
    overflow-y: scroll;
}

@media only screen and (max-width: 500px){
    .Chat { 
        margin-top: 6.5vh;
        height: 68vh;
    }
}



@media only screen and (min-width: 800px) {
    .Chat {
        margin-top: 5vh;
        height: 77.2vh;
    }
}

.Box {
    
    background: gray;
    position: absolute;
    bottom: 0;
    width: 100%;
}

.Top {
    display: flex;
    position: absolute;
    top: 0;
    width: 100%;
    background: gray;
    order: 1000;
}

.scrollbar
{
	margin-left: 30px;
	float: left;
	height: 300px;
	width: 65px;
	background: #F5F5F5;
	overflow-y: scroll;
	margin-bottom: 25px;
}



#style-3::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	background-color: #F5F5F5;
}

#style-3::-webkit-scrollbar
{
	width: 6px;
	background-color: #F5F5F5;
}

#style-3::-webkit-scrollbar-thumb
{
	background-color: #000000;
}
